import { createTheme, ThemeProvider } from "@mui/material/styles";
import React, { useState } from "react";
import { BrowserRouter } from "react-router-dom";
import Dashborad from "./Dashboard/Dashborad";
import LoginForm from "./LoginForm/LoginForm";

const theme = createTheme({
  palette: {
    primary: {
      main: "#FF5656",
      bg: "#FF56564f",
    },
    secondary: {
      main: "#20AAE2",
      bg: "#20AAE24f",
    },
    third: {
      main: "#5F00BA",
      bg: "#5F00BA4f",
    },
    forth: {
      main: "#FB9300",
      bg: "#FB93004f",
    },
  },
});
export const LogginContext = React.createContext({});
function App() {
  const [Logged, setLogged] = useState(localStorage.getItem("logged")  ?? false);

  const Loggin = () => {
    setLogged(true);
    localStorage.setItem("logged", true);
  };
  const Logout = () => {
    setLogged(false);
    localStorage.setItem("logged", false);
  };

  return (
    <ThemeProvider theme={theme}>
      <LogginContext.Provider value={{ Loggin, Logout }}>
        <BrowserRouter>
          {Logged === true ? <Dashborad /> : <LoginForm />}
        </BrowserRouter>
      </LogginContext.Provider>
    </ThemeProvider>
  );
}

export default App;
